@import './partials/variables';
@import './partials/breakpoints';
@import './partials/header';
@import './partials/about';
@import './partials/skills';
@import './partials/featured';
@import './partials/footer';

@import './partials/fancyhr';

.App {
  text-align: center;
  font-family: 'Rubik', sans-serif;
  color: $primary-font-color;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}


body {
  margin: 0;
  padding: 0;

}

a {
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  color: $highlight-color;
}


h1, .section-logo {
  font-size: calc(16px + 1.25vw);
}

h2 {
  font-size: calc(14px + 1vw);
}

p {
  text-align: justify;
}

main {
    margin: 1em;
    }

.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  border-color: #7d97ad;
  margin-bottom: 8px;
  margin-top: 8px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.flex-project {
  float: left;
  width: 100%;
  border: thin solid #ccc;
  border-radius: 2%;
  text-align: center;
  padding: 10px;
  margin-bottom: 5px;
}

.proj-title {
  font-weight: bold;
  margin-bottom: 4px;
}

.proj-img {
  max-width: 100%;
  border-radius: inherit;
}

.proj-links {
  margin: 0;
  padding: 0;
  text-align: center;
}



.certificate-img {
  max-height: 160px;
  width: auto;
  }

  .certificate-img:hover {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }

  .certificate p {
    text-align: center;
  }

@media only screen and (min-width: $screen-md-min) {

  .flex-project {
    width: calc((100% - 48px)/3);
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-style: none;
    margin: 8px;
    min-height: 355px;
  }

  .certificate-img {
    max-height: 200px;
    width: auto;
    }
   .certificates-container {
      margin: 3em auto 4em;
    }

}

@media only screen and (min-width: $screen-lg-min) {

  h2 {
    font-size: 24px;
  }

  .flex-project {
    min-height: 364px;
  }

  .certificates-container {
    width: 90%;
    margin: 3em auto 4.5em;
    display: flex;
    align-items: center;
  }

  .certificate {
    width: 50%;
    border-radius: 6px;
    background-color: white;
  }

  .certificate-img {
    max-height: 250px;
    width: auto;
    }

}

@media only screen and (min-width: $screen-xl-min) {

  main {
    margin: 10px auto 0;
    max-width: $maxwidth-screen-xl-min;
  }

  h2 {
    font-size: calc(16px + 0.75vw);
  }

  .landing-page {
    min-height: 100vh;
  }

  .flex-project {
    width: calc((100% - 90px)/3);
    margin: 15px;
    min-height: 386px;
    padding: 15px;
  }
}

@media only screen and (min-width: $screen-xxl-min) {
  .landing-page {
    min-height: 0;
  }

  main {
    max-width: 72vw;
  }
}

